.AboutContainer {
    background-color: #ECD8CF;
    display: flex;
    flex-direction: column;
    width: 100%;
}

.AboutSpecial {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 100%;
    justify-content: center;
}

.AboutCard {
    display: flex;
    justify-content: center;
    width: 100%;
    margin: 0px 0px 40px 0px;
}

.AboutText {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-self: center;
    align-content: center;
    margin-top: 55px;
    text-align: center;
}

.AboutHeading {
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: center;
    align-content: center;
    color: #ECD8CF;
    background-color: #1D5A40;
    font-size: xx-large;
    width: 80%;
    height: 80px;
    text-align: center;
    font-family: 'Times New Roman', Times, serif;
    border-radius: 10px 10px 10px 10px;
    box-shadow: rgba(0, 106, 44, 0.4) 10px 0px,
    rgba(0, 106, 44, 0.4) -10px 0px,
     rgba(0, 106, 44, 0.3) 20px 0px,
     rgba(0, 106, 44, 0.3) -20px 0px,
     rgba(0, 106, 44, 0.2) 30px 0px,
     rgba(0, 106, 44, 0.2) -30px 0px,
       rgba(0, 106, 44, 0.1) 40px 0px,
       rgba(0, 106, 44, 0.1) -40px 0px,
        rgba(0, 106, 44, 0.05) 50px 0px,
        rgba(0, 106, 44, 0.05) -50px 0px;
}


.AboutPara {
    color: #1D5A40;
    width: 800px;
    margin: 50px 0px 50px 0px;
    font-size: x-large;
    font-family: 'Lucida Sans Unicode';
}

.Aboutimg {
    display: flex;
    position: relative;
    justify-content: flex-end;
    height: 600px;
    width: 700px;
    padding: 50px;
    border-radius: 50px;
    margin: 0px 0px 0px 10px;
}

.aboutImage {
    border: 5px solid #ffffff;
    border-radius: 10%;
}

.Instructor {
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: center;
    align-content: center;
    color: #ECD8CF;
    background-color: #1D5A40;
    font-size: xx-large;
    width: 40%;
    height: 80px;
    text-align: center;
    font-family: 'Times New Roman', Times, serif;
    border-radius: 10px 10px 10px 10px;
    box-shadow: rgba(0, 106, 44, 0.4) 10px 0px,
    rgba(0, 106, 44, 0.4) -10px 0px,
     rgba(0, 106, 44, 0.3) 20px 0px,
     rgba(0, 106, 44, 0.3) -20px 0px,
     rgba(0, 106, 44, 0.2) 30px 0px,
     rgba(0, 106, 44, 0.2) -30px 0px,
       rgba(0, 106, 44, 0.1) 40px 0px,
       rgba(0, 106, 44, 0.1) -40px 0px,
        rgba(0, 106, 44, 0.05) 50px 0px,
        rgba(0, 106, 44, 0.05) -50px 0px;
}

.AboutInstructor {
    display: flex;
    justify-content: center;
}

.InstructorCard0 {
    border-radius: 60px;
    display: grid;
    margin: 40px 50px 40px 50px;
    width: 400px;
    max-width: 30%;
    background-color: #ffffff;
}

.Instructorimg0 {
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: center;
    align-content: center;
    height: 400px;
    width: 400px;
    border-radius: 50px;
}

.InstructorImage0 {
    width: 400px;
    height: 500px;
    border-radius: 50px 50px 0px 0px;
}

.InstructorCard {
    border-radius: 50px;
    display: grid;
    margin: 100px 50px 100px 50px;
    width: 400px;
    max-width: 30%;
    background-color: #ffffff;
}

.Instructorimg{
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: center;
    align-content: center;
    height: 500px;
    width: 400px;
    border-radius: 50px;
}

.InstructorImage {
    width: 400px;
    height: 500px;
    border-radius: 50px 50px 0px 0px;
}

.InstructorHeading0 {
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: center;
    align-content: center;
    padding: 10px;
    text-align: center;
    color: #1D5A40;
    font-size: xx-large;
}

.InstructorHeading {
    padding: 20px 20px 20px 20px;
    text-align: center;
    color: #1D5A40;
    font-size:xx-large;
}

.AboutInstructor {
    color: #1D5A40;
    text-align: center;
    font-size: large ;
    padding: 0px 20px 20px 20px;
}

@media screen and (max-width:1000px) {

    .AboutContainer {
        width: 100%;
        overflow-x: auto;   
    }

    .AboutCard {
        display: grid;
        justify-content: center;
        width: 100%;
        margin: 0px 0px 40px 0px;
    }

    .AboutHeading {
        font-size: x-large;
        width: 50%;
        height: 80px;
    }

    .AboutPara {
        width: 50%;
        font-size: large;
        /* font-family: Montserrat; */
    }

    .Aboutimg {
        display: flex;
        position: relative;
        justify-content: center;
        height: 400px;
        padding: 50px;
        border-radius: 50px;
        margin: 0px 0px 0px 10px;
    }

    .aboutImage {
        border: 5px solid #ffffff;
        border-radius: 10%;
    }

    .Instructor {
        font-size: x-large;
        width: 60%;
        height: 80px;
    }

    .AboutInstructor {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        align-self: center;
        align-content: center;
    }

    .InstructorCard {
        border-radius: 50px;
        display: flex;
        flex-direction: column;
        margin: 100px 0px 100px 0px;
        min-width: 80%;
        max-width: 30%;
        background-color: #ffffff;
    }

    .InstructorCard0 {
        border-radius: 50px;
        display: flex;
        flex-direction: column;
        margin: 100px 0px 100px 0px;
        min-width: 80%;
        max-width: 30%;
        background-color: #ffffff;
    }

    .Instructorimg{
        height: 100%;
        width: 100%;
        border-radius: 50px;
    }

    .InstructorImage {
        width: 100%;
        height: 100%;
        border-radius: 50px 50px 0px 0px;
    }

    .Instructorimg0 {
    height: 100%;
    width: 100%;
    border-radius: 50px;
    }

    .InstructorImage0 {
        width: 100%;
        height: 100%;
        border-radius: 50px 50px 0px 0px;
    }
}